import {Component} from "@angular/core";
import {ConfigurationService} from "../../services/configuration.service";
/**
 * @author rlapin
 */

@Component({
    template: require('./upload.template.html'),
    styles: [require('./upload.style.scss')],
    selector: 'uploadForm',
    providers: [ConfigurationService]
})
export class UploadComponent{
    visible:boolean = false;
    isUpload:boolean = false;
    uploadStatus: string = "";
    private fileToUpload;

    constructor(private configurationService: ConfigurationService){}
    show(){
        this.visible = true;
    }
    close(){
        this.visible = false;    
    }
    uploadFile(){
        var _this = this;
        this.makeFileRequest(this.configurationService.getUrl()+"fileUpload", [], this.fileToUpload).then((result:string) => {
            _this.uploadStatus = result;
        }, (error) => {
             console.log(error);
        });
    }
    fileChangeEvent(fileInput: any){
        this.fileToUpload = fileInput.target.files[0];
    }
    private makeFileRequest(url: string, params: Array<string>, file: File) {
        return new Promise((resolve, reject) => {
            var formData: any = new FormData();
            var xhr = new XMLHttpRequest();
            formData.append("file", file);
            xhr.onreadystatechange = function () {
                if (xhr.readyState == 4) {
                    if (xhr.status == 200) {
                        resolve(xhr.response);
                    } else {
                        reject(xhr.response);
                    }
                }
            };
            xhr.open("POST", url, true);
            xhr.send(formData);
        });
    }
}


