import {Injectable} from "@angular/core";
import {RadarModel,Elem} from "../widgets/radar/radar-model"
/**
 * @author rlapin
 */


@Injectable()
export class DataService {
    getData():RadarModel {
        return {

            value: "Luxoft",
            childs: [{
                value: "Management processes",
                childs: [{
                    value: "Corporate strategy",
                    childs: []
                }, {
                    value: "Financial Management",
                    childs: []
                }, {
                    value: "PR & IR Management",
                    childs: []
                }, {
                    value: "Acquisition & Integration",
                    childs: []
                }
                ]
            }, {
                value: "Business core processes",
                childs: []
            }, {
                value: "Support Processes",
                childs: []
            }]
        }
    }

    randomNode(currentLevel):Elem {
        var childs:Elem[] = [];
        var _this = this
        var number = Math.floor(Math.random() * 5)+5;
        if (currentLevel < 3) {
            for (var i = 0; i < number; i++) {
                childs.push(_this.randomNode(currentLevel + 1));
            }
        }
        return {
            value: "Test",
            childs: childs
        }
    }

    randomData():RadarModel {
        return this.randomNode(0);
    }
}



