import {Component, trigger, state, transition, style, animate} from "@angular/core";
/**
 * @author rlapin
 */


@Component({
    animations: [
        trigger('state', [
            state('inactive', style({
                backgroundColor: '#eee',
                transform: 'scale(1)'
            })),
            state('active',   style({
                backgroundColor: '#cfd8dc',
                transform: 'scale(1.1)'
            })),
            transition('inactive => active', animate('100ms ease-in')),
            transition('active => inactive', animate('100ms ease-out'))
        ])
    ],
    selector: "category-menu",
    template: require('./category-menu.template.html'),
    styles: [require('./category-menu.style.scss')]
})
export class CategoryMenu {

    private categories = [{"name":"All Processes"},
        {"name":"BUSSINESS CORE PROCESSES"},
        {"name":"SUPPORT PROCESSES"},
        {"name":"MANAGEMENT PROCESSES"}];
    private activeCategory = this.categories[0];
    private setActiveCategory(category){
        this.activeCategory = category;
    }
}


